<template>
  <div class="container">
    <div class="data">
      <div class="title">
        <div class="desc">风险信息录入</div>
      </div>
      <div class="table">
        <el-table style="width: 100%" :data="premiumPercentData" :span-method="spanRow" stripe border :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in premiumPercentColumn"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
            <template slot-scope="scope">
              <el-input v-if="column.prop==='premiumPercent'"
                        v-model="scope.row[column.prop]" size="small">
              </el-input>
              <span v-else>{{scope.row[column.prop]}}</span>
            </template>
          </el-table-column>
        </el-table>
        <el-table style="width: 100%" :data="riskOptionData" :span-method="spanRow2" stripe border :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in riskOptionColumn"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
            <template slot-scope="scope">
              <el-checkbox v-if="column.prop==='optionValue'" v-model="scope.row[column.prop]"></el-checkbox>
              <span v-else-if="scope.row.specialTag && column.prop==='info'">{{scope.row[column.prop]}}<el-button @click="specialRiskVisible=true" type="text" icon="el-icon-edit" circle></el-button></span>
              <span v-else>{{scope.row[column.prop]}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="option">
      <back-button>上一步</back-button>
      <back-button type="next" @next="configProtocol">下一步</back-button>
    </div>

    <el-dialog title="特殊风险因子详细信息" :visible.sync="specialRiskVisible">
      <el-row :gutter="20">
        <el-col :span="8"><div class="grid-content bg-purple">&nbsp;</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">风险因子</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">核保人评价</div></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8"><div class="grid-content bg-purple">驾驶员30岁以上年龄占比(%)：</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8"><div class="grid-content bg-purple">每车驾驶员配备：</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8"><div class="grid-content bg-purple">固定资源比例(%)：</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8"><div class="grid-content bg-purple">其他：</div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple"><el-input></el-input></div></el-col>
      </el-row>
      <div slot="footer" style="text-align: center" class="dialog-footer">
        <el-button type="primary" plain @click="specialRiskVisible=false">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BackButton = () => import('@/components/BackButton')
export default {
  name: 'riskInfo',
  components: {BackButton},
  data () {
    return {
      premiumPercentColumn: [
        {prop: 'fleetType', width: 120, label: '车队类型', align: 'center'},
        {prop: 'info', width: 120, label: '信息内容', align: 'center'},
        {prop: 'option', width: 120, label: '选项', align: 'center'},
        {prop: 'premiumPercent', width: 120, label: '保费占比', align: 'center'}
      ],
      premiumPercentData: [
        {fleetType: '营业出租租赁客车', info: '车龄', option: '0-1年', premiumPercent: '50.00'},
        {fleetType: '营业出租租赁客车', info: '车龄', option: '1-2年', premiumPercent: '30.00'},
        {fleetType: '营业出租租赁客车', info: '车龄', option: '2-3年', premiumPercent: '10.00'},
        {fleetType: '营业出租租赁客车', info: '车龄', option: '3-5年', premiumPercent: '0.00'},
        {fleetType: '营业出租租赁客车', info: '车龄', option: '5-8年', premiumPercent: '0.00'},
        {fleetType: '营业出租租赁客车', info: '车龄', option: '8年以上', premiumPercent: '0.00'},
        {fleetType: '营业出租租赁客车', info: '使用性质', option: '出租', premiumPercent: '0.00'},
        {fleetType: '营业出租租赁客车', info: '使用性质', option: '租赁', premiumPercent: '0.00'}
      ],
      riskOptionColumn: [
        {prop: 'fleetType', width: 120, label: '车队类型', align: 'center'},
        {prop: 'info', width: 120, label: '风险描述', align: 'center'},
        {prop: 'optionValue', width: 120, label: '选项值', align: 'center'},
        {prop: 'option', width: 120, label: '选项', align: 'center'}
      ],
      riskOptionData: [
        {fleetType: '营业出租租赁客车', info: '特殊风险因子', optionValue: '', option: '', specialTag: true},
        {fleetType: '营业出租租赁客车', info: '安全奖惩制能够落实', optionValue: '', option: '是', specialTag: false},
        {fleetType: '营业出租租赁客车', info: '安全奖惩制能够落实', optionValue: '', option: '否', specialTag: false},
        {fleetType: '营业出租租赁客车', info: '每日排班出车情况', optionValue: '', option: '1人只白班出车', specialTag: false},
        {fleetType: '营业出租租赁客车', info: '每日排班出车情况', optionValue: '', option: '2人白班夜班轮流出车', specialTag: false}
      ],
      fleetType: '',
      currentRow: 0,
      rowSpan: 0,
      specialRiskVisible: false
    }
  },
  computed: {
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  methods: {
    configProtocol () {
      this.$router.push('/admin/fleet/declare/modify/configProtocol')
    },
    spanRow ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return [8, 1]
        } else {
          return [0, 0]
        }
      } else if (columnIndex === 1) {
        if (rowIndex === 0) {
          return [6, 1]
        } else if (rowIndex === 6) {
          return [2, 1]
        } else {
          return [0, 0]
        }
      }
    },
    spanRow2 ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return [5, 1]
        } else {
          return [0, 0]
        }
      } else if (columnIndex === 1) {
        if (rowIndex === 0) {
          return [1, 1]
        } else if (rowIndex === 1) {
          return [2, 1]
        } else if (rowIndex === 3) {
          return [2, 1]
        } else {
          return [0, 0]
        }
      }
    }
  }
}
</script>

<style scoped>

  .el-row {
    margin-top: 10px;
  }

</style>



